	module.exports = {"pathContext":{"frontmatter":{"path":"/docs/absolute-relative-layout","title":"Absolute/Relative Layout","hasPlayground":true,"initialPlayground":null,"redirect":null},"html":"<h2>Absolute/Relative Layout</h2>\n<p>The <code class=\"language-text\">position type</code> of an element defines how it is\npositioned within its parent.</p>\n<p><strong>RELATIVE (DEFAULT)</strong> By default an element is positioned\nrelatively. This means an element is positioned according to the\nnormal flow of the layout, and then offset relative to that position\nbased on the values of <code class=\"language-text\">top</code>, <code class=\"language-text\">right</code>, <code class=\"language-text\">bottom</code>, and <code class=\"language-text\">left</code>.\nThe offset does not affect the position of any sibling or parent elements.</p>\n<p><strong>ABSOLUTE</strong> When positioned absolutely an element doesn't take\npart in the normal layout flow. It is instead laid out independent\nof its siblings. The position is determined based on the\n<code class=\"language-text\">top</code>, <code class=\"language-text\">right</code>, <code class=\"language-text\">bottom</code>, and <code class=\"language-text\">left</code> values. </p>\n<p><controls prop=\"positionType\"></controls></p>\n<p>The position values <code class=\"language-text\">top</code>, <code class=\"language-text\">right</code>, <code class=\"language-text\">bottom</code>, and <code class=\"language-text\">left</code> behave\ndifferently depending on the <code class=\"language-text\">position type</code> of the element. For\na <code class=\"language-text\">relative</code> element they offset the position of the element in the\ndirection specified. For <code class=\"language-text\">absolute</code> element though these properties\nspecify the offset of the element's side from the same side on the parent.</p>\n<p><controls prop=\"position\"></controls></p>"}}

